.idouban-tabs {
  margin-top: 15px;
  margin-bottom: 15px;
}

.idouban-tab {
  padding: 5px;
  text-decoration: none !important;
}

.idouban-tab-active {
  background: #657b83;
  color: #fff !important;
}

.idouban-item {
  clear: both;
  min-height: 150px;
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
  position: relative;
}

@media screen and (max-width: 600px) {
  .idouban-item {
    width: 100%;
  }
}

.idouban-picture {
  width: 100px;
  position: absolute;
  top: 10px;
  left: 0;
}

.idouban-picture img {
  max-width: 100%;
}

.idouban-info {
  padding-left: 120px;
}

.idouban-meta {
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  margin-top: 5px;
  padding-right: 10px;
  font-size: 12px;
  display: -webkit-box;
  overflow: hidden;
}

.idouban-rating {
  margin-top: 5px;
  padding-right: 10px;
  font-size: 12px;
  display: -webkit-box;
  overflow: hidden;
}

.idouban-comment {
  margin-top: 5px;
  font-size: 14px;
}

.idouban-pagination {
  text-align: center;
  margin-top: 15px;
  margin-bottom: 10px;
}

.idouban-page-num {
  min-width: 60px;
  display: inline-block;
}

.idouban-button {
  padding: 5px;
  text-decoration: none !important;
}

.idouban-button:hover {
  opacity: 1;
  background: #657b83;
  color: #fff !important;
}

.idouban-hide {
  display: none;
}

.idouban-show {
  display: block;
}

